<template lang="pug">
  #store.wrapper
    navbar
    main
      button.btn_backToTop.mb_hide(@click="backToTop")
      ad
      mainContent
</template>

<script>
import $ from 'jquery';
import navbar from '../components/navbar.vue';
import ad from '../components/ad.vue';
import mainContent from '../components/store_grid/mainContent.vue';

export default {
  components: {
    navbar,
    ad,
    mainContent,
  },
  methods: {
    backToTop() {
      $('html, body').animate({ scrollTop: 0 }, 200);
    },
  },
  mounted() {
    const $win = $(window);
    const $backToTop = $('.btn_backToTop');
    $win.scroll(() => {
      if ($win.scrollTop() > 200) {
        $backToTop.show();
      } else {
        $backToTop.hide();
      }
    });
  },
};
</script>

